
const mixin = {
    data() {
        return {
            prodcolumns,
            prodrowSelection,
            proddata: [],
            pagination: {
                showQuickJumper: true,
                defaultPageSize: 10,
                showTotal: (total) => `共 ${total} 条数据`,
                showSizeChanger: true,
                defaultCurrent: 1,
                pageSizeOptions: ["10", "15", "20"],
                onShowSizeChange: (current, pageSize) => (this.pageSize = pageSize),
            },
            list: []
        }

    },
    methods: {
        onEdit(id) {
            this.$router.push({
                path: "/editor",
                query: {
                    id: id,
                },
            });
        },
        onDetails(id) {
            this.$router.push({
                path: "/viewdetails",
                query: {
                    id: id,
                },
            });
        },
        search() { //搜索
            let list = this.proddata.filter(
                (item) => item.goodsName.indexOf(this.productname) >= 0
            );
            this.proddata = list;
            this.productname = "";
        },
        reset() { //重置
            this.productlist();
        },
        onclickAdded() {  //新增
            this.$router.push({
                path: "/newly",
            });
        },
        async productlist() {  //商品列表

            let str = {
                merId: sessionStorage.getItem("id"),
                pageNum: 1,
                pageSize: 100,
            };
            let { data } = await this.$http.list(str);
            if (data.code == "0000") {
                console.log(data);
                let tableData = data.list
                tableData.reverse()
                if (tableData.length > 10) {
                    tableData.map(item => {
                        this.list.map(str => {
                            if (item.classId == str.id) {
                                item.classId = str.className
                            }
                        })
                    })
                    this.proddata = tableData.slice(0, 10)
                    this.status()
                    setTimeout(() => {
                        for (let i = 11; i < tableData.length; i++) {
                            this.proddata.push(tableData[i])
                        }
                        this.status()
                    })

                } else {
                    this.proddata = tableData
                    tableData.map(item => {
                        this.list.map(str => {
                            if (item.classId == str.id) {
                                item.classId = str.className
                            }
                        })
                    })
                    this.status()
                }
            }

        },
        async classlist() {  //商品分类
            let { data } = await this.$http.classlist();
            this.list = [...data.list];
        },
        status() {
            this.proddata.map((item) => {
                switch (item.status) {
                    case 0:
                        item.status = "录入";
                        break;
                    case 1:
                        item.status = "通过审核";
                        break;
                    case 2:
                        item.status = "审核驳回";
                        break;
                }
            })
        },
        async onShelves(id) { //商品上架
            let str = {
                id: id,
            };
            let { data } = await this.$http.shelves(str);
            console.log(data);
            if (data.code == "0000") {
                this.productlist();
            }
        },
    }
}
export default mixin

//商品页面数据
