<template>
  <div>
    <!-- 商品列表 -->
    <div class="productlist-header">
      <a-breadcrumb separator=">">
        <a-breadcrumb-item>商品管理</a-breadcrumb-item>
        <a-breadcrumb-item>{{ this.$route.meta.title }}</a-breadcrumb-item>
      </a-breadcrumb>
    </div>
    <hr style="color: #666" />
    <div class="productlist-cenent">
      <div>
        <span style="margin-left: 10px">商品搜索：</span>
        <input type="text" placeholder="商品名称" v-model="productname" />
        <button @click="search">搜索</button>
        <button @click="reset">重置</button>
      </div>
      <div>
        <button @click="onclickAdded">+&nbsp;新增商品</button>
      </div>
      <a-table
        :columns="prodcolumns"
        :data-source="proddata"
        :pagination="pagination"
        :row-selection="prodrowSelection"
        bordered
        rowKey="id"
      >
        <span slot="operation" slot-scope="text, record">
          <a @click="onEdit(record.id)">编辑</a>
          <a-divider type="vertical" />
          <a @click="onDetails(record.id)">查看详情</a>
          <a-divider type="vertical" />
          <a @click="onShelves(record.id)" v-show="proddata.isShelves != 1"
            >上架</a
          >
          <a @click="onPutdown(record.id)" v-show="proddata.isShelves == 1"
            >下架</a
          >
        </span>
      </a-table>
    </div>
  </div>
</template>

<script>
import mixin from "@/utils/mixin.js";
export default {
  mixins: [mixin],
  data() {
    return {
      productname: "",
    };
  },
  mounted() {
    this.productlist();
    this.classlist()
  },
  methods: {
   
  },
};
</script>

<style scoped>
.productlist-cenent {
  width: 100%;
  margin-top: 10px;
}
.productlist-cenent > :nth-child(1) {
  width: 100%;
  height: 60px;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  background: #f5f7fa;
}
.productlist-cenent > :nth-child(1) input {
  width: 300px;
  border: 1px solid #666;
  outline: none;
}
.productlist-cenent > :nth-child(1) button {
  margin-left: 10px;
}
.productlist-cenent > :nth-child(2) {
  width: 100%;
  height: 27px;
  margin-bottom: 10px;
}
.productlist-header {
  height: 10px;
  margin-bottom: 18px;
}
</style>